// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import store from './store'
import { sync } from 'vuex-router-sync'
import router from './router'
import './common/resources.js'
import interceptors from './common/interceptors.js'
import FbFilters from './common/filters.js'
import FbDirectives from './common/directives.js'
import SimpleVueValidation from 'simple-vue-validator'
import { getReadTime } from './common/utils.js'
import Env from './env.js'

import {
  Cell,
  CellSwipe,
  Field,
  Header,
  Button,
  Toast,
  MessageBox,
  Indicator,
  Progress,
  Tabbar,
  TabItem,
  Spinner,
  InfiniteScroll
} from 'mint-ui'
Vue.component(Cell.name, Cell)
Vue.component(CellSwipe.name, CellSwipe)
Vue.component(Field.name, Field)
Vue.component(Header.name, Header)
Vue.component(Button.name, Button)
Vue.component(Progress.name, Progress)
Vue.component(Tabbar.name, Tabbar)
Vue.component(TabItem.name, TabItem)
Vue.component(Spinner.name, Spinner)
Vue.use(InfiniteScroll)

// icon
const ToastClasses = {
  'success': 'iconfont ui-icon-success',
  'error': 'iconfont ui-icon-warn',
  'warn': 'iconfont ui-icon-warn-block'
}

Vue.$toast = Vue.prototype.$toast = function(msg = '', type = '') {
  Toast({
    message: msg,
    duration: getReadTime(msg),
    iconClass: type ? ToastClasses[type] : ''
  })
}
Vue.$messagebox = Vue.prototype.$messagebox = MessageBox
Vue.$indicator = Vue.prototype.$indicator = Indicator

sync(store, router)

Vue.use(FbFilters)
Vue.use(FbDirectives)

// 
Vue.use(SimpleVueValidation, {
  templates: {
    error: '',
    required: '',
    float: '',
    integer: '',
    number: '',
    lessThan: '{0}',
    lessThanOrEqualTo: '{0}',
    greaterThan: '{0}',
    greaterThanOrEqualTo: '{0}',
    between: '{0}{1}',
    size: '{0}',
    length: '{0}',
    minLength: '{0}',
    maxLength: '{0}',
    lengthBetween: '{0}{1}',
    /* beautify ignore:start */
    in: '{0}',
    /* beautify ignore:end */
    notIn: '{0}',
    match: '',
    regex: '',
    digit: '',
    email: '',
    url: 'url',
    optionCombiner: function(options) {
      if (options.length > 2) {
        options = [options.slice(0, options.length - 1).join(', '), options[options.length - 1]]
      }
      return options.join('')
    }
  }
})

// http
const envApiPaths = {
  'development': '/api.aspx',
  'production': '/api.aspx',
  'app-development': 'http://fn.91zhengxin.com:9111/api.aspx',
  'app-production': 'http://fn.91zhengxin.com:9111/api.aspx'
}
Vue.http.options.root = envApiPaths[process.env.NODE_ENV] || '/api.aspx'
  // Vue.http.headers.common['Authorization'] = window.localStorage.token || ''

// 
interceptors.forEach((v) => {
  Vue.http.interceptors.push(v)
})

// ioscssactive
document.body.addEventListener('touchstart', () => {})

// bfcache
window.onpageshow = function(event) {
  if (event.persisted) {
    window.location.reload()
  }
}

function main() {
  /* eslint-disable no-new */
  new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: { App }
  })
}

// 
if (process.env.NODE_ENV.indexOf('app') > -1) {
  document.addEventListener('deviceready', e => {
    main()
    const AppUpdater = require('./app.updater.js').default
    new AppUpdater({
      root: Env.getManifestServer()
    })
  })
} else {
  main()
}



