import Vue from 'vue'
import VueResource from 'vue-resource'
Vue.use(VueResource)

const previousRequestMap = {}
const commonOpts = {
  emulateJSON: true,
  before(request) {
    const key = Vue.url(request.url, request.params)
    if (previousRequestMap[key]) {
      previousRequestMap[key].abort()
    }

    previousRequestMap[key] = request
  }
}

const resource = Vue.resource
export const loginByPhone = resource('?method=loginByPhone', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const sendMsgFromPhone = resource('?method=sendMsgFromPhone', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const updateInfoSendMsgFromPhone = resource('?method=updateInfoSendMsgFromPhone', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const validateUpdateInfoMsgFromPhone = resource('?method=validateUpdateInfoMsgFromPhone', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const updateBankInfo = resource('?method=updateBankInfo', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const queryMyAccount = resource('?method=queryMyAccount', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const getCustState = resource('?method=getCustState', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const isDetectionBankCard = resource('?method=isDetectionBankCard', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const SetAgreementMsg = resource('?method=SetAgreementMsg', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const SetAgreementSign = resource('?method=SetAgreementSign', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const QueryContractList = resource('?method=QueryContractList', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const QueryContract = resource('?method=QueryContract', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const CustRepayment = resource('?method=CustRepayment', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const PayContract = resource('?method=PayContract', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const GiveUpContract = resource('?method=GiveUpContract', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 
export const Messages = resource('?method=Messages', {}, {}, { _showLoadingStatus: true, ...commonOpts }) // 



